@import url(https://fonts.googleapis.com/css2?family=Esteban&family=Montserrat:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Esteban&family=Montserrat:wght@900&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  /*font-family: 'Signika', sans-serif;*/
  font-family: 'Esteban', serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content-wrapper {
  overflow: auto;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
}

.titlebar {
    display: flex;
    background-color: #011936;
    font-family: 'Montserrat', sans-serif;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 75px;
    position: fixed;
    width: 100%;
    height: 120px;
}

.icons {
    justify-content: center;
    align-self: center;
}

.icons img {
    margin-right: 20px;
    height: 40px;
    width: auto;
}

.links {
    align-self: center;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.section-link {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.section-link a {
    color: white;
    text-decoration: none;
    margin-right: 20px;
    font-size: x-large;
}

.section-link a:hover {
    color: #011936;
}

.section-link:hover{
    background-color: white;
}
.greeting-wrapper {
    height: 100vh;
    background-image: url(/portfolio/static/media/mthoodgray.35670155.png);
    margin-top: 120px;
}

.greeting {
    width: 25%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    margin-left: 5%;
}

.greeting img {
    width: 50%;
    height: auto;
    border-radius: 50%;
    border: white 5px solid;
}

.greeting p {
    text-align: center;
    color: white;
    font-size: larger;
}
.projects-wrapper {
    background-color: white;
    scroll-snap-align: start;
    min-height: auto;
    background-color: #011936;
}

.project-list {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 120px;
}
.project-card {
    background-color: white;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 400px;
    width: 400px;
    margin: 15px;
    padding: 5px;
}

.project-description{
    text-align: center;
}
.contact {
    height: 100vh;
    background-color: #465362;
    display: flex;
    justify-content: center;
}

.contact-form-wrapper {
    display: flex;
    justify-content: center;
    margin-top: 120px;
    background-color: white;
    width: 300px;
    height: 300px;
    border-radius: 5%;
}

form {
    display: flex;
    flex-direction: column;
}

#submit-button {
    margin-top: 30px;
}
